import { Box, Button, Flex } from "@chakra-ui/react";
import React from "react";
import { HashLink } from "react-router-hash-link";
import "./Button.css";
import "animate.css";
import './Button.css'




const Navbar = () => {
  return (
    <Box
      id="nav-menu"
      p="20px 10px"
    //   background={"#360148"}
      background={"#0F2027"}
      color={"white"}
    //   border={"1px solid black"}
      position={"sticky"}
      top="0"  
    >
      <Flex justifyContent={"space-between"} alignItems={"center"}>
        <Box w="25%" textAlign={"center"}>
          Sharvari
        </Box>
        <Box w="50%">
          <Flex justifyContent={"space-between"} alignItems={"center"}>
            <HashLink
              className="linkEffect"

              // style={{ color: "white", textDecoration: "none" }}
              to="/"
            >
              Home
            </HashLink>
            <HashLink
             className="linkEffect"
              // className=x  "animate__animated animate__pulse"
              style={{ color: "white", textDecoration: "none" }}
              to="/#about"
            >
              About
            </HashLink>
            <HashLink
             className="linkEffect"
              className="animate__animated animate__pulse"
              style={{ color: "white", textDecoration: "none" }}
              to="/#skills"
            >
              Skills
            </HashLink>
          
            <HashLink
             className="linkEffect"
              style={{ color: "white", textDecoration: "none" }}
              to="/#projects"
            >
              Projects
            </HashLink>

            <HashLink
             className="linkEffect"
              style={{ color: "white", textDecoration: "none" }}
              to="/#contact"
             
            >
              Contact
            </HashLink>
            
            <Button
              bgGradient="linear(to-l,#304352, #3fada8)"
              mr="20px"
              _hover={{
                bgGradient: "linear(to-r,#3fada8, purple.500)",
              }}
            >
              Resume
            </Button>
           
          </Flex>
        </Box>
      </Flex>
    </Box>
  );
};

export default Navbar;
